import React, { useState, useEffect } from "react";
import styled from "styled-components";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { useNavigate } from "react-router-dom";
import CommentForm from "./CommentForm";
import CommentList from "./CommentList"; // Quill Editor  .
import "react-quill/dist/quill.snow.css";
import boardApi from "../../api/boardApi";
import commentApi from "../../api/commentApi";
import HorizontalLine from "Components/common/HorizontalLine"
// WritingMainPage.js

interface PostInfo {
  id: number
  title: string
  content: string
  userName: string
  startDate: string
  commentSum: number
  category: string,
  updatedAt: string
}

/////////,/////////
const FormContainer = styled.div`
  max-height: 30rem; /*     */
  max-width: 70rem;
  padding-left: 1%;
  padding-right: 1%;
  overflow-y: auto; /*    */
`;

const TitleInput = styled.input`
  border: none; /*    */
  width: 99%;
  height: 2rem;
  font-size: 1.3rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #ccc;
  outline: none;
`;
const CustomQuillEditor = styled(ReactQuill)`
  /*     */

  .ql-editor {
    min-height: 28rem; /*    */
  }

  .ql-container {
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .ql-toolbar {
    /*    */
    background-color: #ccc; /*     */
    border-radius: 5px; /*      */
  }
`;

/////////////////////////
const PostsButtonContainer = styled.div`
  display: flex;
  justify-content: flex-end;
`;
const PostsButton = styled.button`
  width: 5.5rem;
  height: 2rem;
  margin: 0.5%;
  font-size: 1rem;
  border-radius: 1rem;
  background-color: #ff530e;
  color: white;
  font-weight: bold;
  border: none;
  transition: background-color 0.3s;

  /*      */

  &:hover {
    background-color: #ff7c7c;
    color: white;
    cursor: pointer;
  }
`;
////////// //////////
const ViewTitleInput = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 0.1rem;
  padding-right: 1rem;
  padding-left: 1rem;
`;

const Title = styled.span`
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0.5rem 0 0.5rem 0;
`;

const AuthorAndDate = styled.span`
  font-size: 0.7rem;
  color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: top;
`;
///////////////////
const Content = styled.div`
  padding-right: 1rem;
  padding-left: 1rem;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
  min-height: 10rem;
  .ql-font-serif {
    font-family: Georgia, Times New Roman, serif, "Courier New", Courier,
      monospace;
  }
  .ql-size-huge {
    font-size: 2.5em;
  }
  .ql-size-large {
    font-size: 1.5em;
  }
  .ql-size-small {
    font-size: 0.75em;
  }
`;

interface PostData {
  commentId: number,
  title: string,
  content: string,
  author: string,
  date: string,
  commentCount: number,
  category: string,
  updatedAt: string
}

const ViewWritingPage = ({ selectedRowId, projectId, postId }
  : { selectedRowId: number, projectId: number, postId: number }) => {
  const [editorHtml, setEditorHtml] = useState(""); // Quill Editor HTML   
  const [title, setTitle] = useState(""); //   
  const [showViewWriting, setShowViewWriting] = useState(true);
  const [showPutWriting, setShowPutWriting] = useState(false);
  const [selectedPost, setSelectedPost] = useState({
    commentId: 0,
    title: "",
    content: "",
    author: "",
    date: "",
    commentCount: 0,
    category: "",
    updatedAt: ""
  });
  const navigate = useNavigate();

  const goToPreviousPage = () => {
    setTimeout(function () {
      window.location.reload();
    }, 100);
  };
  const goToHome = () => {
    navigate(`/manage/${projectId}`);
  };

  // //   
  const putWiring = () => {
    // HTML    
    const strippedHtml = editorHtml.replace(/<[^>]+>/g, "");

    //      
    if (!title.trim() || !strippedHtml.trim()) {
      alert("   .");
      return; //   
    }

    const updatedPostData = {
      projectId: projectId,
      postId: selectedRowId,
      title: title,
      content: editorHtml,
      category: selectedPost.category, //   category  
      updatedAt: selectedPost.updatedAt,
    };

    // axios  PUT  
    boardApi
      .putBoard(updatedPostData)
      .then((response) => {
        console.log(response.data);
        alert("   .");
        setTitle(""); //  
        setEditorHtml("");

        if (postId) {
          goToHome();
        } else {
          goToPreviousPage();
        }
      })
      .catch((error) => {
        console.error("Error updating post:", error);
        alert("    .");
      });
  };

  //  
  const deletePost = () => {
    const token = sessionStorage.getItem('login-token');
    console.log(token);

    if (!token) {
      alert(' .');
      return;
    }

    boardApi
      .deleteBoard({
        data: {
          projectId: projectId,
          postId: selectedRowId
        }
      })
      .then(() => {
        alert("  .");
        if (postId) {
          goToHome();
        } else {
          goToPreviousPage();
        }
      })
      .catch((error) => {
        console.error("Error deleting post:", error);
        alert("    .");
      });
  };

  const changePutView = () => {
    setTitle(selectedPost.title);
    setEditorHtml(selectedPost.content);

    setShowViewWriting(false);
    setShowPutWriting(true);
  };
  //      DB  set

  useEffect(() => {
    //  API   
    const fetchData = async () => {
      try {
        const [postResponse] = await Promise.all([
          boardApi.getBoard({ projectId: projectId, postId: selectedRowId }),
        ]);
        // postResponse 

        const postInfo: PostInfo = postResponse.data.data;
        setSelectedPost({
          commentId: postInfo.id,
          title: postInfo.title,
          content: postInfo.content,
          author: postInfo.userName,
          date: postInfo.startDate,
          commentCount: postInfo.commentSum,
          category: postInfo.category,
          updatedAt: postInfo.updatedAt,
        });
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, [selectedRowId, projectId]);

  const checktoken = () => {
    console.log(sessionStorage.getItem('login-token'))
  }

  //  
  const updatedAtDate = new Date(selectedPost.updatedAt);
  const formattedUpdatedAt = `${updatedAtDate.getFullYear()} ${String(updatedAtDate.getMonth() + 1).padStart(2, '0')} ${String(updatedAtDate.getDate()).padStart(2, '0')} ${String(updatedAtDate.getHours()).padStart(2, '0')}:${String(updatedAtDate.getMinutes()).padStart(2, '0')}:${String(updatedAtDate.getSeconds()).padStart(2, '0')}`;

  // showViewWriting +  showPutWriting
  return (
    <>
      {showViewWriting ? (
        <>
          <FormContainer>
            <ViewTitleInput>
              <Title>{selectedPost.title}</Title>
              <AuthorAndDate>
                : {selectedPost.author} | : {selectedPost.date}
              </AuthorAndDate>
              <AuthorAndDate>
                <span>  : {formattedUpdatedAt}</span>
              </AuthorAndDate>
            </ViewTitleInput>
            <HorizontalLine />
            <Content
              dangerouslySetInnerHTML={{ __html: selectedPost.content }}
            />
          </FormContainer>
          <PostsButtonContainer>
            <PostsButton onClick={changePutView}></PostsButton>
            <PostsButton onClick={deletePost}></PostsButton>
            {postId ? (
              <PostsButton onClick={goToHome}></PostsButton>
            ) : (
              <PostsButton onClick={goToPreviousPage}></PostsButton>
            )}
          </PostsButtonContainer>
        </>
      ) : showPutWriting ? (
        <>
          <FormContainer>
            <TitleInput
              type="text"
              placeholder=" "
              value={title}
              onChange={(e) => setTitle(e.target.value)} //     title  
            />
            <CustomQuillEditor
              value={editorHtml}
              onChange={setEditorHtml}
              modules={{
                toolbar: [
                  ["bold", "italic", "underline", "strike"], //  
                  // [{'list': 'ordered'}, {'list': 'bullet'}],
                  // ['image', 'video'], //   
                  [{ font: [] }], //  
                  [{ size: ["small", false, "large", "huge"] }], //  
                  ["clean"],
                ],
              }}
            />
          </FormContainer>
          <PostsButtonContainer>
            <PostsButton onClick={putWiring}></PostsButton>
            <PostsButton onClick={goToPreviousPage}></PostsButton>
          </PostsButtonContainer>
        </>
      ) : null}
    </>
  );
};

export default ViewWritingPage;