import React, { useState, useEffect } from "react";
import Body from "../../Components/common/Body";
import styled from "styled-components";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { useNavigate } from "react-router-dom";
import boardApi from "../../api/boardApi";
import axios from "axios";
import { TableText, TextMd } from "Components/common/Font";
import InputText from "Components/common/InputText";
import Button from "Components/common/Button";
import NewButton from "Components/common/NewButton";
import { theme } from "LightTheme";
import { left } from "@popperjs/core";

const FormContainer = styled.div`
  display:flex;
  flex-direction:column;
  max-height: 30rem; /*    */
  max-width: 70rem;

  padding: 0 0.5rem;
  overflow-y: auto; /*    */
`;

const CustomQuillEditor = styled(ReactQuill)`
  /*     */

  .ql-editor {
    min-height: 20rem; /*    */
  }

  .ql-container {
    border: 1px solid #ccc;
    border-radius: 15px;
  }

  .ql-toolbar {
    /*    */
    background-color: rgba(0, 0, 0, 0.03);
    border-radius: 15px;
  }
`;


const WritingTitle=styled.span`
  font-size: 1rem;
  font-weight: 600;
  color: black;
`;

const PostsButtonContainer = styled.div`
  display: flex;
  justify-content: flex-end;
`;

const WritingPage = ({ projectId, category }:{projectId:number,category:string}) => {
  const [editorHtml, setEditorHtml] = useState(""); // Quill Editor HTML   
  const [title, setTitle] = useState(""); //   

  const navigate = useNavigate();
  const goToPreviousPage = () => {
    setTimeout(function () {
      window.location.reload();
    }, 100);
  };

  const handleContentChange=(content:string)=>{
    setTitle(content);
  };

  const addPost = async () => {
    // HTML    
    const strippedHtml = editorHtml.replace(/<[^>]+>/g, "");

    //      
    if (!title.trim() || !strippedHtml.trim()) {
      alert("   .");
      return; //   
    }

    const postData = {
      projectId: projectId,
      title: title,
      content: editorHtml,
      category: category, //  PLANNING .   .
    };

    try {
      //  API   
      //
      const response = await boardApi.postBoard(postData);
      if (response.data.success) {
        alert("  .");
        setTimeout(function () {
          window.location.reload();
        }, 100);
        //   (:      )
        setTitle("");
        setEditorHtml("");
        //         
      } else if (response.data.success === false) {
        if (response.data.code === 7000) {
          alert("    .");
          navigate("/LoginPage");
        } else if (response.data.code === 7001) {
          alert(" .   .");
          //  
          sessionStorage.removeItem("login-token");
          delete axios.defaults.headers.common["Authorization"];
          navigate("/LoginPage");
        }
        else if (response.data.code === 8000) {
          alert(" " + response.data.message); // "  ."
        }
      } else {
        alert("    .");
      }
    } catch (error) {
      console.error("Error creating post:", error);
      alert("    .");
    }
  };

  return (
    <>
      <FormContainer>
        <WritingTitle style={{margin:'0.5rem 0'}}></WritingTitle>
        <InputText
          placeholder=" "
          value={title}
          onChange={handleContentChange} //     title  
          width={"99%"} height="2rem"/>
        <CustomQuillEditor
          value={editorHtml}
          onChange={setEditorHtml}
          modules={{
            toolbar: [
              ["bold", "italic", "underline", "strike"], //  
              // [{'list': 'ordered'}, {'list': 'bullet'}],
              ["video"], //   
              [{ font: [] }], //  
              [{ size: ["small", false, "large", "huge"] }], //  
              ["clean"],
            ],
          }}
        />
      </FormContainer>
      <PostsButtonContainer>
        <NewButton onClick={addPost} textcolor="white" backcolor={theme.color.orange} width={"6rem"} height={"2rem"} style={{marginLeft:'1rem'}}></NewButton>
        <NewButton onClick={goToPreviousPage} textcolor="black" backcolor={theme.color.white} width={"6rem"} height={"2rem"}></NewButton>
      </PostsButtonContainer>
    </>
  );
};

export default WritingPage;
