import React, {useEffect, useState} from "react";
import styled from "styled-components";
import Body from "../../../Components/common/Body";
import BoardPage from "../common/BoardPage";
import {useNavigate, useParams} from "react-router-dom";
import boardApi from "../../../api/boardApi";
import axios from "axios";

const MakingNoticeMainpage = () => {
    const {projectId, postId} = useParams() as {projectId:any,postId:any};
    const navigate = useNavigate();
    const getPostsByCategory = async (category:string) => {
        try {
            const response = await boardApi.getBoardList(projectId, category);
            if (response.data && response.data.success === false) {
                if (response.data.code === 7000) {
                    alert("    .");
                    navigate("/LoginPage");
                } else if (response.data.code === 7001) {
                    alert(" .   .");
                    //  
                    sessionStorage.removeItem("login-token");
                    delete axios.defaults.headers.common['Authorization'];
                    navigate("/LoginPage");
                }
                return;
            }
            return response.data; //   
        } catch (error) {
            console.error('    .', error);
            throw error;
        }
    };
    const subTitle = "";
    const writingButtonContent = "";
    const category = "PRODUCTION";

    const [tableData, setTableData] = useState([]);
    const fetchData = async () => {
        try {
            const posts = await getPostsByCategory(category);

            setTableData(posts.list);
        } catch (error) {
            console.error('    .', error);
            //  
        }
    };

    useEffect(() => {
        fetchData();
    }, []);

    return (
        <Body>
            <BoardPage subTitle={subTitle}
                       tableData={tableData}
                       writingButtonContent={writingButtonContent}
                       projectId={projectId}
                       postId={postId}
                       category={category}/>
        </Body>
    );
};
export default MakingNoticeMainpage;
